import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {Control} from "core/ui/controls/control"
import {WordImages} from "../wordImages"

export class PaperComponent extends GameComponent {
  paper = this.addImage([WordImages.paper, [0, 0]])

  positions = Sizes.create({
    paperHeight: 650,
    heightDiff: 30
  })

  updatePaper(group: Control) {
    this.paper.key = this.gameKey(group.height > this.positions.paperHeight ? WordImages.paper_big : WordImages.paper)
  }

  onPropsUpdate() {
  }
}



