import {createLevelsFromArray} from "core/types/logic"

export type TaskType = "synonym" | "antonym" | "unrelated"

export interface Level {
  types: TaskType[]
  wrongCount: number
  answerCount: number
}

function level(types: TaskType[], answerCount: number, wrongCount: number): Level {
  return {types, answerCount, wrongCount}
}

export const levels = createLevelsFromArray([
  level(["synonym"], 1, 2),
  level(["antonym"], 1, 2),
  level(["unrelated"], 1, 2),
  level(["synonym", "antonym"], 1, 3),
  level(["antonym", "unrelated"], 2, 3),
  level(["synonym", "unrelated"], 3, 3),
  level(["synonym", "antonym", "unrelated"], 1, 5),
  level(["synonym", "antonym", "unrelated"], 2, 5),
  level(["synonym", "antonym", "unrelated"], 3, 5)
])



