import {translationService} from "core/services"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {Sizes} from "core/ui/common/sizes"
import {screenSizeService} from "core/ui/services/instances"
import {BaseWordMainComponent} from "games/polyglot/common/components/baseWordMainComponent"
import {QuestionComponent} from "games/polyglot/journalist/components/questionComponent"
import {WordListComponent} from "games/polyglot/journalist/components/wordListComponent"
import {Translations} from "games/polyglot/journalist/translations"
import {TypeWriterComponent} from "../components/typeWriterComponent"
import {Task} from '../logic'

export class MainComponent extends BaseWordMainComponent {
  positions2 = Sizes.create({
    descY: 200,
  })

  positionsPortrait2 = Sizes.update(this.positions2, {
    descY: 150,
  })

  getPositions2 = () => screenSizeService.isPortrait ? this.positionsPortrait2 : this.positions2 // move to gameComponent
  enableHint = true
  private question = this.mainGroup.add(this.createComponent(QuestionComponent))
    .updatePos(Sizes.w2, this.getPositions2().descY)
    .anchorCenter()

  onInit(): void {
    this.positions = Sizes.update(this.positions, {
      mainGroup: {y: 130},
      typewriter: [1000, 257],
    })
    this.positionsPortrait = Sizes.update(this.positionsPortrait, {
      wordList: [Sizes.widthNormal / 2, 870],
      mainGroup: {y: 110},
      typewriter: [650, 180],
    })
    this.typewriter = this.mainGroup.add(this.createComponent(TypeWriterComponent))
      .setPos(this.getPositions().typewriter)
    this.hintComponent.show = false
    super.onInit()
  }

  createWordListComponent() {
    return this.createComponent(WordListComponent)
  }

  createWordListProps(wordListData: any) {
    return wordListData
  }

  onAnswer(answer: ILastAnswer) {
    if (this.isTutorial && answer.status == AnswerStatus.Error) {
      this.tutorial.setProps(
        translationService.getTranslation(
          this.getCurrentTask<Task>().taskType == "synonym"
            ? Translations.tutorialSynonym
            : Translations.tutorialAntonym)
      )
    }

  }

  onNewTask(task: Task) {
    this.question.setProps(task)
    this.renderTask(task.answers, task)
    if (this.isTutorial) {
      this.tutorial.setProps(translationService.getTranslation(Translations.title))
      this.bringToTop(this.tutorial)
    }
    this.mainGroup.bringToTop(this.question)
  }

}



