import {CoreImages} from "core/assets/images"
import {Cnst} from "core/logic/cnst"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {TextPanelComponent} from "core/ui/components/games/gameUI/textPanelComponent"
import {OnInit} from "core/ui/components/onInit"
import {Btn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {Img} from "core/ui/controls/img"
import {screenSizeService} from "core/ui/services/instances"

const durations = {
  hide: 300,
}

export class HintComponent extends GameComponent<string> implements OnInit {
  positions = Sizes.create({
    margin: 10,
    tooltipSize: {width: 880, height: 120},
    tooltip: {x: 60, y: 0},
  })
  hintCount = Cnst.hintCount

  private hints: Img[]
  btn: Btn
  private textPanel: TextPanelComponent

  onAnswer(answer: ILastAnswer) {
    if (AnswerStatus.isMain(answer.status)) {
      this.btn.disabled = false
    }
  }

  onInit(): void {
    const group = new Control()
    const main = group.add(new Img(CoreImages.hint_main))
    this.hints = CoreImages.hintList.map(x => new Img(x))
    group.addChildren(this.hints)
    this.btn = this.add(new Btn("", () => this.hint(), undefined, group)).anchorCenter()
    this.btn.onHover(out => {
      main.key = out ? CoreImages.hint_main : CoreImages.hint_hover
      this.textPanel.visible = !out
    })
    this.onChangeFor(() => screenSizeService.isPortrait, x => this.defaultPosition())
    this.textPanel = this.add(new TextPanelComponent({size: this.positions.tooltipSize})).setPos(this.positions.tooltip)
    this.textPanel.visible = false
  }

  defaultPosition() {
    const topOffset = screenSizeService.topOffset
    this.updatePos(
      -screenSizeService.realLeftOffset + this.btn.width / 2 - this.positions.margin,
      -topOffset + this.btn.height / 2 - this.positions.margin)
    return this
  }

  hint() {
    if (!this.context.inputService.enabled) {
      return
    }
    this.hintCount = this.hintCount - 1
    const hide = this.hintCount <= 0
    this.btn.disabled = true
    this.hints[this.hintCount].visible = false
    this.context.uiService.hintComponent.onHint.dispatch()
    if (hide) {
      Tweens.start({
        target: this.btn,
        duration: durations.hide,
        to: {symmetricScale: 0},
        easing: Phaser.Easing.Back.In,
        onComplete: () => this.destroy(true)
      })
    }
  }

  onPropsUpdate() {
    this.textPanel.setProps(this.props)
  }

  beforeDestroy() {
    this.context.uiService.hintComponent.onHint.dispose()
  }
}



