import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {FocusBehavior} from "core/ui/controls/behaviors/focusBehavior"
import {Control} from "core/ui/controls/control"
import {TutorialState} from "core/ui/logic/tutorialState"
import {Undef} from "core/utils/undef"
import {AnagrammaComponent} from "games/polyglot/common/components/anagrammaComponent"
import {PaperComponent} from "games/polyglot/common/components/PaperComponent"
import {Anagramma} from "../logic"

type Props = { list: Anagramma[], answerWord: string, enableHint: boolean, offset: { x: number, y: number } }

export class WordListComponent extends GameComponent<Props, TutorialState> implements OnInit {
  positions = Sizes.create({
    wordMargin: {x: 0, y: 15},
    focus: [400, 400],
    focusSize: {width: 500, height: 400}
  })

  paper = this.add(this.createComponent(PaperComponent))
  wordsGroup = this.add(new Control()).anchorCenter()
  private words: AnagrammaComponent[]

  onInit(): void {
    if (this.isTutorial) {
      this.onChangeFor(() => this.tutorialStore.isLastStep, isLastStep => {
        this.setAnswerHightlightDisabled(!isLastStep)
      })
    }
  }

  setAnswerHightlightDisabled(disabled: boolean) {
    this.words.forEach(x => x.setAnswerHightlightDisabled(disabled))
  }

  onPropsUpdate() {
    this.wordsGroup.removeAll(true)

    const smallFont = this.props.list.some(anagramma => anagramma.anagramma.length > 10)
    this.words = this.props.list.map((anagramma, index) => {

        const component = this.createComponentWithProps(AnagrammaComponent,
          {anagramma, index, enableHint: this.props.enableHint, smallFont})
        component.tag = anagramma.word == this.props.answerWord
        return component
      }
    )

    this.wordsGroup.addChildren(this.words)
    Sizes.tableColumn(this.words, this.positions.wordMargin)
    this.paper.updatePaper(this.wordsGroup)
    this.wordsGroup.updatePos(
      this.center()[0] + this.props.offset.x,
      this.center()[1] + this.props.offset.y)
    this.wordsGroup.updatePivot()
    this.updatePivot()
    Undef.map(this.wordsGroup.getBehavior(FocusBehavior), x => {
      x.visible = false
      x.updateSize()
      this.context.uiService.mainComponent.onAnswersAreReady.addOnce(() => x.visible = true)
    })

  }
}



