import {LevelNumber, TaskCount} from "core/types/logic"

export type LevelsTaskCount = {
  taskCount: TaskCount
  maxLevel: LevelNumber
}

export interface TaskCountCreator {
  get(startLevel: number): number
}

export class LevelsTaskCountCreator implements TaskCountCreator {
  get(startLevel: number): number {
    const taskCount = this.levels.find(x => startLevel <= x.maxLevel)
    return taskCount ? taskCount.taskCount : 0
  }

  constructor(private levels: LevelsTaskCount[]) {

  }
}





