import {CoreSounds} from "core/assets/sounds"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Signals} from "core/ui/animations/signals"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {HintComponent} from "core/ui/components/games/gameUI/hintComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {IntroComplexAnimationBehavior} from "core/ui/controls/behaviors/introComplexAnimationBehavior"
import {Control} from "core/ui/controls/index"
import {screenSizeService} from "core/ui/services/instances"
import {BaseTypeWriterComponent} from "games/polyglot/common/components/baseTypewriterComponent"
import {WordListComponent} from "games/polyglot/common/components/wordListComponent"
import {WordsSounds} from "games/polyglot/common/wordsSounds"
import {WordImages} from "../wordImages"

const durations = {
  wordList: 300,
}

export abstract class BaseWordMainComponent extends BaseMainGameComponent implements OnInit {

  positions = Sizes.create({
    wordMargin: {x: 0, y: 80},
    wordList: [650, Sizes.heightNormal / 2 + 10],
    typewriter: [1000, 260],
    mainGroup: {y: 0},
    tutorial: {y: 40},
    animationX: 0,
    bgitem: [350, 1050],
    wordlistOffset: {x: 0, y: 0}
  })

  positionsPortrait = Sizes.update(this.positions, {
    wordList: [Sizes.widthNormal / 2, 770],
    typewriter: [650, 80]
  })

  getPositions = () => screenSizeService.isPortrait ? this.positionsPortrait : this.positions

  bg = this.addImage([WordImages.bg, [0, 0]])
  bgitem = screenSizeService.isPortrait || this.isTutorial ? undefined : this.addImage([WordImages.bgitem, [0, 0]]).setPos(this.positions.bgitem)
  mainGroup = this.add(new Control())
  wordList: GameComponent
  typewriter: BaseTypeWriterComponent
  useTutorial = false
  hintComponent = {
    enableLetterHint: false,
    tooltip: "",
    show: true
  }
  hint: HintComponent

  tutorial: TutorialTextComponent

  createTutorial() {
    return this.add(this.createComponent(TutorialTextComponent)).make({marginTop: this.getPositions().tutorial.y})
  }

  onInit(): void {
    this.playSound(CoreSounds.bg, true)
    this.statusOptions.waitCompleteBeforeNewTask = true
    this.startIntro()
    if (this.hintComponent.show) {
      const hint = this.mainGroup.add(this.createComponent(HintComponent)).defaultPosition()
      this.hint = hint
      hint.setProps(this.hintComponent.tooltip)
      hint.visible = false
      if (!this.isTutorial) {
        this.context.uiService.mainComponent.onIntroComplete.addOnce(() => {
          hint.visible = true
        })
      }
    }

    if (this.isTutorial) {
      this.tutorial = this.createTutorial().updateFields({visible: false})
      this.context.uiService.mainComponent.onIntroComplete.addOnce(() => {
        CommonAnimations.setVisible(this.tutorial, true)
      })
      this.mainGroup.y = this.getPositions().mainGroup.y
    }
    this.onChangeFor(() => screenSizeService.isPortrait, x => {
      const p = this.getPositions()
      this.typewriter.setPos(this.getPositions().typewriter)
      if (this.wordList) {
        this.wordList.setPos(this.getPositions().wordList)
      }

    })
  }

  introAnimation() {
    const behavior = new IntroComplexAnimationBehavior()
    this.addBehavior(behavior)
    return behavior
  }

  wordListAnimation(obj: Control, remove: boolean) {
    obj.visible = true
    const y = remove ? (Sizes.height + obj.height / 2) : (this.cameraOffset.y - obj.height / 2)
    const to = {x: this.positions.animationX, y}
    Tweens.start({
      target: obj,
      duration: durations.wordList,
      easing: Phaser.Easing.Sinusoidal.InOut,
      to,
      reverse: !remove,
      onComplete: () => {
        if (remove) {
          obj.destroy(true)
        } else {
          this.answersAreReady()
        }
      }
    })
  }

  createWordListComponent(): GameComponent {
    const component = this.createComponent(WordListComponent)
    return component
  }

  createWordListProps(wordListData: any, answerWord: string): any {
    return {
      list: wordListData,
      enableHint: this.hintComponent.enableLetterHint,
      answerWord,
      offset: this.positions.wordlistOffset
    }
  }

  renderTask(words: string[], wordListData: any) {
    this.typewriter.clearText()
    this.typewriter.setProps(words)
    if (this.wordList) {
      this.wordListAnimation(this.wordList, true)
    }
    this.wordList = this.mainGroup.add(this.createWordListComponent()).anchorCenter()
    this.wordList.visible = false
    this.wordList.setPos(this.getPositions().wordList)
    this.wordList.setProps(this.createWordListProps(wordListData, words[0]))
    const signal = this.isFirstTask ? this.context.uiService.mainComponent.onIntroComplete : Signals.memorize()
    signal.addOnce(() => {
      this.playSound(WordsSounds.paper)
      this.wordListAnimation(this.wordList, false)
    })
    if (this.hint) {
      this.mainGroup.bringToTop(this.hint)
    }

  }

}



