import {ITask} from "core/logic/index"
import {GameLogic} from "core/types/gameLogic"
import {UniqSeq} from "core/utils/circleSeq"
import {RandomGenerator} from "core/utils/index"
import {Numbers} from "core/utils/numbers"
import {tuple4} from "core/utils/tuples"
import {EnSynonymsWords, SynonymsWordSet} from "games/polyglot/journalist/enVersion/enWords"
import * as _ from "lodash"
import {Level, TaskType} from "../levels"

export interface Task extends ITask {
  word: string
  answerWords: string[]
  taskType: TaskType
  answers: string[]
}

export class EnLogic {
  rng = new RandomGenerator() // [4, 2,8,10]
  uniqSeq = new UniqSeq(10, this.rng)
  tutorialTaskTypes: TaskType[] = ["synonym", "antonym"]
  tutorialSeq = new UniqSeq(this.tutorialTaskTypes.length, this.rng)

  possibleAnswersAndWrongWords(items: SynonymsWordSet, type: TaskType, rest: SynonymsWordSet[]) {
    const synonyms = items[0].concat(items[1])
    const antonyms = items[2]
    const wrongWord = _.flatten(this.rng.sample(rest))
    const [answer, ...restSynonyms] = this.rng.shuffle(synonyms)
    if (type == "synonym") {
      return tuple4(answer, restSynonyms, antonyms, wrongWord)
    } else if (type == "antonym") {
      return tuple4(answer, antonyms, restSynonyms, wrongWord)
    } else {
      return tuple4(answer, wrongWord, restSynonyms, antonyms)
    }
  }

  items = EnSynonymsWords.items("en")

  nextTask(level: Level, isTutorial: boolean): Task {
    const items = this.items.filter(x =>
      x[0].length + x[1].length > level.answerCount + 1
      && x[2].length >= level.answerCount
    )

    const wordSet = this.uniqSeq.nextFrom(items)
    const rest = items.filter(x => x != wordSet)
    const taskType = isTutorial
      ? this.tutorialSeq.nextFrom(this.tutorialTaskTypes)
      : this.rng.sample(level.types)
    const [word, possibleAnswers, wrong1, wrong2] = this.possibleAnswersAndWrongWords(wordSet, taskType, rest)
    if (wrong1.length + wrong2.length < level.wrongCount) {
      return this.nextTask(level, isTutorial)
    }
    const [wrong1Count, wrong2Count] = Numbers.splitEven(level.wrongCount, 2)
    const wrong1Words = this.rng.subset(wrong1, wrong1Count)
    const wrongWords = wrong1Words.concat(this.rng.subset(wrong2, level.wrongCount - wrong1Words.length))
    const answers = this.rng.subset(possibleAnswers, level.answerCount)
    const answerWords = this.rng.shuffle(answers, wrongWords)

    return {
      word,
      taskType,
      answerWords,
      answers
    }
  }

}





