import {Locale} from "core/logic/cnst"
import {Strings} from "core/utils/strings"
import {WordsEn} from "games/polyglot/journalist/wordsEn"
import {LocaleUtl} from "games/utils/localeUtl"
import * as _ from 'lodash'

export enum WordLength {
  Small, Middle, Big
}

export type SynonymsWordSet = string[][]

export module EnSynonymsWords {

  const localeData = {
    en: WordsEn.data
  }

  function fix(data: typeof WordsEn.data) {
    return data.map(x => x.map(y => _.uniq(Strings.splitTrim(y).filter(x => x != "")).map(x => _.capitalize(x))))
      .filter(x => x.length == 3)
  }

  export const items = (locale: Locale) => fix(LocaleUtl.getData(localeData, locale))

  // console.log(items.map(x => _(x).flatten().groupBy(x => x).value()))

}



