import {CoreColors} from "core/assets/fonts"
import {AnswerId, AnswerStatus, ILastAnswer} from "core/types/logic"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {AnswerBtnBehavior} from "core/ui/controls/behaviors/answerBtnBehavior"
import {BtnAlphaHover} from "core/ui/controls/behaviors/btnAlphaHoverBehavior"
import {Control} from "core/ui/controls/control"
import {Btn} from "core/ui/controls/index"
import {Arrays} from "core/utils/arrays"
import {Fonts} from "games/polyglot/common/fonts"
import {WordImages} from "games/polyglot/common/wordImages"
import * as _ from "lodash"
import {Anagramma} from "../logic"

const maxColumn = 5

type Props = { anagramma: Anagramma, index: number, enableHint: boolean, smallFont: boolean }

export class AnagrammaComponent extends GameComponent<Props> implements OnInit {
  positions = Sizes.create({
    wordMargin: {y: 0},
    wordWidth: 450
  })
  private buttons: Btn[]
  poloska = this.addImage([WordImages.poloska, [0, 0]])
  buttonGroup = this.add(new Control()).anchorCenterX().updatePos(this.width / 2, 0)

  onInit(): void {
    this.context.uiService.hintComponent.onHint.add(() => this.showHint())
  }

  showHint() {
    if (this.props.enableHint) {
      const button = this.buttons.find(x => x.text == this.props.anagramma.word[0])!
      button.textControl.fill = CoreColors.hint
    }
  }

  showTutorial() {
    const buttons = this.buttons.filter(x => this.getCurrentTask().answers)
  }

  onAnswer(answer: ILastAnswer) {
    if (answer.status == AnswerStatus.Correct || answer.status == AnswerStatus.Error && !this.isTutorial) {
      // this.removeAll(true)
      // this.add(new Txt(this.props.word, Fonts.wordText))

      this.createButtons(true)
      this.updatePositions()
    }
  }

  updatePositions() {
    const buttons = Arrays.chunkEven(this.buttons, this.props.smallFont ? 8 : 6)
    Sizes.table(buttons, {
      x: this.positions.wordWidth,
      isFullWidth: true,
      y: 0
    })
    this.poloska.y = this.buttonGroup.height
    // Sizes.tableColumn([this.buttonGroup, this.poloska], {x:0,y:-50})
  }

  setAnswerHightlightDisabled(disabled: boolean) {
    this.buttons.forEach(x => x.getBehavior(AnswerBtnBehavior).setDisabled(disabled))
  }

  createButtons(showWord: boolean) {
    this.buttonGroup.removeAll(true)
    this.buttons = Arrays.zip(this.props.anagramma.anagramma.split(""), this.props.anagramma.word.split("")).map(([aLetter, wLetter], i) => {
        let txt = i == this.props.anagramma.hideIndex ? "?" : aLetter
        txt = showWord ? wLetter : txt == " " ? "_" : txt
        const btn = new Btn(txt, () => {
        }, Fonts.wordText(this.props.smallFont ? 2 : 3))
        const answerId = (aLetter + this.props.index.toString()) as AnswerId
        return this.addAnswerBehavior(btn, answerId, {dark: true, location: "bottom"}).addBehavior(new BtnAlphaHover())
      }
    )
    this.buttonGroup.addChildren(this.buttons)
    this.updatePositions()
    this.buttonGroup.updatePivot()

  }

  onPropsUpdate() {
    this.createButtons(false)
  }
}




