import * as F from "core/assets/fonts"
import {WithNumIndex} from "core/types/common"

export module Fonts {
  const wordFontSizes: WithNumIndex<number> = {1: 50, 2: 70, 3: 90}
  export const wordText = (size: number) => ({
    fontSize: (wordFontSizes[size]),
    font: F.CoreFonts.MontserratSemiBold,
    fill: F.CoreColors.lightBlack
  })
  export const typewriterText = {
    fontSize: 50, font: F.CoreFonts.MontserratSemiBold, fill: F.CoreColors.black, align: "center",
    wordWrap: true
  }
}



