import {CoreColors} from "core/assets/fonts"
import {GameComponentContext} from "core/types/logic"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Sizes} from "core/ui/common/sizes"
import {OnInit} from "core/ui/components/onInit"
import {Behavior} from "core/ui/controls/behaviors/behavior"
import {PointerBehavior} from "core/ui/controls/behaviors/pointerBehavior"
import {Btn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"

export class FocusBehavior extends Behavior<Control> implements OnInit {
  positions = Sizes.create({
    radius: 5,
    margin: 40,
    pointerMaring: 40,
    border: 20
  })
  back: Phaser.Graphics
  btn: Btn

  private pointerBehavior = new PointerBehavior(this.context,
    {location: "top", dark: true, margin: this.positions.pointerMaring})

  get visible() {
    return this.btn.visible
  }

  set visible(v) {
    if (v) {
      CommonAnimations.setVisible(this.btn, v)
      CommonAnimations.setVisible(this.pointerBehavior.arrow, v)
    } else {
      this.pointerBehavior.arrow.visible = false
      this.btn.visible = false

    }
  }

  constructor(private context: GameComponentContext, public callback?: () => void) {
    super()
  }

  updateSize() {
    this.back.clear()
    this.back.lineStyle(this.positions.border, CoreColors.focus, 1)
    this.back.drawRoundedRect(0, 0, this.control.width + this.positions.margin, this.control.height + this.positions.margin,
      this.positions.radius)
    const topLeft = this.control.calcTopLeftOnParent(this.context.uiService.rootComponent)
    if (!topLeft) {
      return
    }
    const {x, y} = topLeft
    const [cx, cy] = this.control.center()
    this.btn.updatePos(x + cx, y + cy)
    this.btn.updateButtonSize()
    this.btn.updatePivot()
    this.pointerBehavior.updateArrowPosition()
  }

  onInit() {
    const rect = this.control.game.make.graphics()
    const group = new Control()
    group.addChild(rect)
    this.back = rect
    this.btn = new Btn("", () => this.callback ? this.callback() : undefined, undefined, group).anchorCenter()

    this.context.uiService.rootComponent.addChild(this.btn)
    CommonAnimations.scalePulse(this.btn)
    this.control.addBehavior(this.pointerBehavior)
    this.updateSize()
  }

}



