import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {Signals} from "core/ui/animations/signals"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {Control} from "core/ui/controls/control"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {Img} from "core/ui/controls/img"
import {Txt} from "core/ui/controls/txt"
import {screenSizeService} from "core/ui/services/instances"
import {tuple} from "core/utils/tuples"
import {Fonts} from "games/polyglot/common/fonts"
import {WordsSounds} from "games/polyglot/common/wordsSounds"
import {WordImages} from "../wordImages"

const durations = {
  hand: 100,
  type: 100
}
const secondHandSound = "secondHandSound"

export class BaseTypeWriterComponent extends GameComponent<string[]> implements OnInit {

  positions = Sizes.create({
    text: tuple(0, 0),
    maxWordWidth: 500,
    handsY: 510,
    handsX: [90, 370]
  })

  alwaysFullWord = false

  private typeMain: Img
  private text: Txt
  private hands: Img[]

  onInit(): void {
    this.cloneSound(WordsSounds.type, secondHandSound)
    this.typeMain = this.addImage([WordImages.type_main, [0, 0]])
    this.text = this.add(new Txt("", Fonts.typewriterText)).setPos(this.positions.text).anchorCenter()
  }

  addHands() {
    this.hands = [WordImages.hand_0, WordImages.hand_1].map((x, i) => this.addImage([x, [this.positions.handsX[i], Sizes.height]]))
  }

  index = 1

  animationClean() {
  }

  clearText() {
    if (this.text.text) {
      this.text.text = ""
      this.animationClean()
    }
  }

  handAnimation(handIndex: number, repeat = 0) {
    if (!screenSizeService.isPortrait) {
      const hand = this.hands[handIndex]
      const type = this.handTypeAnimation(handIndex, repeat)
      const handShow = this.handShowAnimation(hand, true)
      handShow.chain(
        type,
        this.handShowAnimation(hand, false)
      ).start()
      return (repeat > 0 ? handShow : type).onComplete
    } else {
      return Signals.memorize()
    }
  }

  handShowAnimation(hand: Control, show: boolean) {
    return Tweens.start({
      target: hand,
      duration: durations.hand,
      to: {y: show ? this.positions.handsY : Sizes.height},
      notStart: true
    })
  }

  handTypeAnimation(handIndex: number, repeat = 0) {
    const soundKey = handIndex ? secondHandSound : WordsSounds.type
    this.playSound(soundKey)
    return Tweens.start({
      target: this.hands[handIndex],
      duration: durations.hand,
      to: {symmetricScale: 0.9},
      simpleYoyo: true,
      repeat,
      notStart: true
    })
  }

  onAnswer(answer: ILastAnswer) {
    if (this.alwaysFullWord) {
      if (!this.isTutorial || answer.status != AnswerStatus.Error) {
        const answerText = AnswerStatus.isCorrect(answer.status) ? answer.answerId : this.props.join("\n")
        this.typeWord(answerText as string)
      }
    } else {
      if (answer.status == AnswerStatus.Error) {
        if (!this.isTutorial) {
          const answerText = this.props[0]
          this.typeWord(answerText as string)
        }
      } else {
        this.index = (this.index + 1) % 2
        this.handAnimation(this.index).addOnce(() => this.text.text =
          this.props[0].substring(0, answer.correctAnswerIndex! + 1))
      }

    }
  }

  typeWord(word: string) {
    this.handAnimation(1, 1).addOnce(() => {
      this.text.text = word
      ControlUtl.scaleToFit(this.text, this.positions.maxWordWidth)
      this.text.updatePivot()
    })
    this.delay(durations.type).addOnce(() => this.handAnimation(0, 1))

  }

  onPropsUpdate() {
    this.text.text = this.props[0]
    ControlUtl.scaleToFit(this.text, this.positions.maxWordWidth)
    this.text.updatePivot()
    this.text.text = ""
  }
}




