import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {Img} from "core/ui/controls/img"
import {tuple} from "core/utils/tuples"
import {BaseTypeWriterComponent} from "games/polyglot/common/components/baseTypewriterComponent"
import {Images} from "../images"

const durations = {
  typePart: 150
}

export class TypeWriterComponent extends BaseTypeWriterComponent {
  positions2 = Sizes.create({
    partAnimation: -100,
    center: [383, 395]
  })
  private typePart1: Img
  private typePart0: Img

  onInit(): void {
    this.alwaysFullWord = true

    this.positions = Sizes.update(this.positions, {
      text: tuple(383, 220),
      maxWordWidth: 460,
      handsY: 450
    })
    this.typePart1 = this.addImage([Images.type_part_1, [0, 0]])
      .updateAnchor(0.5, 0.912).setPos(this.positions2.center)
      .fluentUpdate(x => x.scaleY = 0)
    super.onInit()
    this.typePart0 = this.addImage([Images.type_part_0, [0, 0]])
      .updateAnchor(0.5, 0.088).setPos(this.positions2.center)

    this.addHands()
    this.context.uiService.mainComponent.onIntroComplete.addOnce(() => this.animationParts())
  }

  animationParts() {
    const tweenUp = Tweens.start({
      target: this.typePart0,
      duration: durations.typePart,
      notStart: true,
      easing: Phaser.Easing.Sinusoidal.In,
      to: {scaleY: 0}
    })
    const tweenDown = Tweens.start({
      target: this.typePart1,
      duration: durations.typePart,
      notStart: true,
      easing: Phaser.Easing.Sinusoidal.Out,
      to: {scaleY: 1}
    })
    tweenUp.chain(tweenDown).start()

  }

  animationClean() {
  }
}



