import {LevelsTaskCountCreator} from "core/logic/taskCount"

const levelsTaskCount = [
  {taskCount: 8, maxLevel: 3},
  {taskCount: 10, maxLevel: 5},
  {taskCount: 12, maxLevel: 10},
]
const taskCount = new LevelsTaskCountCreator(levelsTaskCount)

export const metaData = {
  unlimitTime: true,
  darkBg: true,
  ignoreError: true,
  noResetSeqCountOnError: true,
  //selectLocale: true,
  taskCount,
  size: {
    width: 2048,
    height: 1280,
    heightMin: 1152
  }
}



