import {IGameMetaData} from "core/logic/index"
import {LevelOptions, ScoreOptions} from "core/types/index"
import * as Common from "games/polyglot/common/commonMeta"
import {WordsSounds} from "games/polyglot/common/wordsSounds"
import {Images} from "games/polyglot/journalist/images"
import {MainComponent} from "./components/mainComponent"
import {levels} from "./levels"
import {Logic} from "./logic"

export const gameId = "journalist"
const scoreOptions: ScoreOptions = {
  scoreAnswer: 100,
  scoreBonus: 0,
  scoreBonusPerLevel: 5
}
const levelOptions: LevelOptions = {
  levels,
  levelsSeqCounts: [{seqCount: 3, maxLevel: 10}]
}
export const metaData: IGameMetaData = {
  ...Common.metaData,
  gameId,
  title: "Journalist",
  logic: Logic,
  images: Images.data,
  component: MainComponent,
  // selectLocale: true,
  tutorialTaskCount: 2,
  sounds: WordsSounds.keys,
  atlasCount: 4,
  scoreOptions,
  levelOptions
}



