import {CoreFonts} from "core/assets/fonts"
import {translationService} from "core/services/instances"
import {Sizes} from "core/ui/common/sizes"
import {PanelComponent} from "core/ui/components/games/gameUI/panelComponent"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {HightlightBehavior} from "core/ui/controls/behaviors/highlightBehavior"
import {Control} from "core/ui/controls/control"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {Txt} from "core/ui/controls/txt"
import {screenSizeService} from "core/ui/services/instances"
import {TaskType} from "games/polyglot/journalist/levels"
import {Task} from "games/polyglot/journalist/logic"
import {Translations} from "games/polyglot/journalist/translations"

const taskTypeTranslation: {[key in TaskType]: [string, string]} = {
  "antonym": [Translations.antonym, Translations.antonyms],
  "synonym": [Translations.synonym, Translations.synonyms],
  "unrelated": [Translations.unrelated, Translations.unrelateds]
}

export class QuestionComponent extends GameComponent<Task> implements OnInit {
  positions = Sizes.create({
    size: {width: 800, height: 120},
    textMargin: 20,
    margin: {x: 4}
  })

  positionsPortrait = Sizes.update(this.positions, {
    size: {width: 600, height: 120},
  })

  getPositions = () => screenSizeService.isPortrait ? this.positionsPortrait : this.positions
  group: Control
  private questionLeft: Txt
  private questionRight: Txt
  hightlight: HightlightBehavior

  onInit(): void {
    const wordBack = this.add(new PanelComponent())
    wordBack.setProps({size: this.getPositions().size})
    const font = {...CoreFonts.tutorial(false), wordWrap: false}
    this.questionLeft = new Txt("", font)
    this.questionRight = new Txt("", font)
    if (this.isTutorial) {
      this.hightlight = new HightlightBehavior()
      this.questionRight.addBehavior(this.hightlight)
      this.hightlight.enableHighlight(true)
    }
    const texts = [this.questionLeft, this.questionRight]
    this.group = this.add(new Control()).setPos(wordBack.center()).anchorCenter()
    this.group.addChildren(texts)
  }

  onPropsUpdate() {
    const task = this.props

    const [taskNameIndex, countTxt] = task.answers.length > 1 ? [1, task.answers.length + " "] : [0, ""]
    const taskNameKey = taskTypeTranslation[task.taskType][taskNameIndex]

    this.questionLeft.text = task.word + "  "
    this.questionRight.text = countTxt + translationService.getTranslation(taskNameKey)
    Sizes.tableRow([this.questionLeft, this.questionRight], this.positions.margin)
    ControlUtl.scaleToFit(this.group, this.getPositions().size.width - this.positions.textMargin)
    this.group.updatePivot()
  }
}




