import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {Btn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {Numbers} from "core/utils/numbers"
import {PaperComponent} from "games/polyglot/common/components/PaperComponent"
import {Fonts} from "games/polyglot/common/fonts"
import {Level} from "games/polyglot/journalist/levels"
import {Task} from "games/polyglot/journalist/logic"

export class WordListComponent extends GameComponent<Task> {
  positions = Sizes.create({
    wordMargin: {x: 0, y: 20},
    wordMarginPlus: 30,
    offset: 25,
    textWidth: 540
  })
  paper = this.add(this.createComponent(PaperComponent))
  wordsGroup = this.add(new Control()).anchorCenter()
  private words: Btn[]

  onAnswer(answer: ILastAnswer) {
    const level: Level = answer.levelTask.level as any
    if (AnswerStatus.isCorrect(answer.status) && level.answerCount > 1) {
      const btn = this.words.find(x => x.text == answer.answerId)!
      btn.visible = false
    }
  }

  onPropsUpdate() {
    this.wordsGroup.removeAll(true)

    this.words = this.props.answerWords.map(word => {
        const size = 1
        const btn = new Btn(word, () => {
        }, Fonts.wordText(size))
        btn.textControl.wordWrap = true
        btn.textControl.wordWrapWidth = this.positions.textWidth
        btn.updateButtonSize()
        btn.alpha = 0.5
        btn.onHover(out => btn.alpha = out ? 0.5 : 1)
        ControlUtl.scaleToFit(btn, this.positions.textWidth)
        return this.addAnswerBehavior(btn, word, {dark: true, location: "left"})
      }
    )

    this.wordsGroup.addChildren(this.words)
    const plus = this.positions.wordMarginPlus * Numbers.coef(this.words.length, 3, 7)
    Sizes.tableColumn(this.words, {x: 0, y: this.positions.wordMargin.y + plus})
    this.paper.updatePaper(this.wordsGroup)
    this.wordsGroup.updatePos(this.center()[0] + this.positions.offset, this.center()[1])
    this.wordsGroup.updatePivot()
    this.updatePivot()
  }
}




